import React, { Component, useEffect, useState } from 'react';
import "./header.css";
import logo from './logo.png';
import {Link} from 'react-router-dom';



class Header extends Component {
  
    render() {  

        return (
            <div >
              
                <div className='navbar1'>
                    
                    
                    <div className='links'>
                        <Link to='/' >
                            <img src={logo} alt="logo" className='logo1'/>
                        </Link>
                        <Link to='/products' className='header_links1'>
                            <p  className='header_links' >Products</p>
                        </Link>
                        
                        <Link to='/about' className='header_links'>
                            <p  className='header_links' >About</p>
                        </Link>
                        
                        <Link to='/plan' className='header_links'>
                            <p  className='header_links' >Plan</p>
                        </Link>
                        
                        
                    </div>
                    
            </div>
        </div>
        );
    }
}

export default Header;