import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import "./home.css";
import logo from './logo.png';
import love from './love.png';
import {Link} from 'react-router-dom';
var cake = () => {
    localStorage.setItem('title', 'Cakes')
    localStorage.setItem('sub-title', 'Read about our cake')
    localStorage.setItem('button', 'Cake')
    localStorage.setItem('to', '/cake')
    console.log(localStorage)
}

var cookie = () => {
    localStorage.setItem('title', 'Cookies')
    localStorage.setItem('sub-title', 'Read about our cookies')
    localStorage.setItem('button', 'Cookies')
    localStorage.setItem('to', '/cookie')
    console.log(localStorage)
}
class Home extends Component {
    state = {
        title: localStorage.getItem('title'),
        sub: localStorage.getItem('sub-title'),
        button: localStorage.getItem('button'),
        to: localStorage.getItem('to')
        
    }
    
    style_p_hover = {
        color: 'blue',
    }
    
    render() {   
        
        return (
            
            <div className='header_img'>
                <div className='header' >
                    <div className='products'>
                        <h1 className='text-header' >{this.state.title}
                        </h1>
                        <h2 className='text-subheader'>
                            {this.state.sub}
                        </h2>
                        
                        
                    </div>
                    
                </div>
                <div className='top1'>
                    <div className='cake'style={{paddingTop: '1%'}}>

                        <h1>I can create the perfect cake for you for any occasion. Weather it is a birthday, wedding or you just fancey some cake I have the perfect design for you...</h1>
                        <Link to='/cake'><button tyle="text-decoration: none;" className="more-week" onClick={cake} >Read More </button>
                        </Link>
                    
                            
                            

                    </div>
                    <div className='cookie' style={{paddingTop: '1%'}}>

                        <h1>No-matter if it is valintines day or christmas I have got you covered with a wide range of cutters and designs I will surley have the right design for your order...</h1>
                        <Link to='/cookie'><button tyle="text-decoration: none;" className="more-week1" onClick={cookie}>Read More </button></Link>

                            
                            

                    </div>
                    <div className='cookie' style={{paddingTop: '1%'}}>

                        <h1>With collection across Bath, Uttoxer and delivery across to uk I will be sure to get you something nice to eat no-matter where you are from in the uk...</h1>
                        <Link to='/about'><button tyle="text-decoration: none;" className="more-week1">Read More </button></Link>

                            
                            

                    </div>

               </div>
                <div className='us'>
                    <Link to='/contact' className='us-buttons'>
                        <div >
                            Contact me
                        </div>
                    </Link>
                    
                    <div className='us-buttons' id='about'>
                        About me
                    </div>
                </div>
             
                <div className='thank'>
                    <div className='img'></div>
                    <h1 className='text'>A big thank you to all our coustomers your support is always very much appriciated</h1>
                </div>
                
            </div>
        );
    }
}

export default Home;