import React, { Component, useEffect, useState } from 'react';
import "./products.css";
import arrow from './arrow.png';
import {Link} from 'react-router-dom';




var cake = () => {
    localStorage.setItem('title', 'Cakes')
    localStorage.setItem('sub-title', 'Read about our cake')
    localStorage.setItem('button', 'Cake')
    localStorage.setItem('to', '/cake')
    console.log(localStorage)
}

var cookie = () => {
    localStorage.setItem('title', 'Cookies')
    localStorage.setItem('sub-title', 'Read about our cookies')
    localStorage.setItem('button', 'Cookies')
    localStorage.setItem('to', '/cookie')
    console.log(localStorage)
}

class Products extends Component {
  
    render() {  

        return (
            <div className='top'>
                <div className='cake'>
                    
                    <h1>I can create the perfect cake for you for any occasion. Weather it is a birthday, wedding or you just fancey some cake I have the perfect design for you...</h1>
                    <Link to='/cake'><button tyle="text-decoration: none;" className="more-week" onClick={cake}>Read More </button>
                    </Link>
                  
                        
                        

                </div>
                <div className='cookie'>
                    
                    <h1>No-matter if it is valintines day or christmas I have got you covered with a wide range of cutter and designs I will surley have the right design for your order...</h1>
                    <Link to='/cookie'><button tyle="text-decoration: none;" className="more-week1"onClick={cookie}>Read More </button></Link>

                        
                        

                </div>

        </div>
        );
    }
}

export default Products;