import React, { Component, useEffect } from 'react';
import "./contact.css";
import logo from './logo.png';
import {Link} from 'react-router-dom';
import axious from 'axios'


class Plan extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: localStorage.getItem('name'),
            order: localStorage.getItem('order'),
            description: localStorage.getItem('description')
        };
    
        
        this.handleChange = this.handleChange.bind(this);
        this.handleChange1 = this.handleChange.bind(this);
        this.handleChange2 = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }
      
    handleChange = (event) => {
        event.preventDefault()
        this.setState({[event.target.name]: this.state.first_name = event.target.value});
    }
    handleChange1 = (event) => {
        event.preventDefault()
        this.setState({[event.target.name]: this.state.first_name = event.target.value});
    }
    handleChange2 = (event) => {
        event.preventDefault()
        this.setState({[event.target.name]: this.state.first_name = event.target.value});
    }
    
    handleSubmit = () =>{
        
        localStorage.setItem('name', this.state.name);
        localStorage.setItem('order', this.state.order);
        localStorage.setItem('description', this.state.description);

    }
    render() {  

        return (
            <div  >
              
                
                    <h1 className='contact-me'>Plan Your Order</h1>
                    <h2 className='sub-title'>Fill in the information about your order save it then come back any time to change it.</h2>
                    <div className='Contact'>
                        <form onSubmit={this.handleSubmit} className='contact-form'>
                            <div>
                                <div>
                                    <input autocomplete="off" type="text" value={this.state.name} onChange={this.handleChange1} name='name' placeholder='Name your order' className='Email'/>
                                </div>
                                <input autocomplete="off" placeholder='What is your order for' value={this.state.order} onChange={this.handleChange1} name='order' className='Email'/>
                            </div>
                            <textarea autocomplete="off" placeholder='Describe your order' value={this.state.description} onChange={this.handleChange1} name='description' className='Message'/>
                            <button className='submit' type='submit' value='Submit'>Save</button>
                            
                        </form>
                        <div className='padding'></div>
                            
                    </div>
                    
                    
            </div>
        );
    }
}

export default Plan;