import React, { Component, useEffect, useState } from 'react';
import "./home.css";
import {Link} from 'react-router-dom';



class Footer extends Component {
  
    render() {  

        return (
            <div><div className='home-div'>
                <h1 className='home'>Back to home</h1>
                <div  className='home-link'>
                    <Link to='/'className='home-button'>HOME</Link>
                </div>
            
                    </div>
                    
                    <div className='links1'>
                        <Link className='link1'><p className='link1'>Products</p></Link>
                        <Link className='link'><p className='link'>About </p></Link>
                        <Link className='link'><p className='link'>Plan</p></Link>
                        
                        
                        
                    </div>
                    <div className='bottom'>
                        <h3 className='credit'>Made by Oliver Weightman</h3>
                
                        
                    </div>
                </div>

        );
    }
}

export default Footer;